import React, { useState, useEffect } from "react";
import MUIDataTable from "mui-datatables";
import axios from '../../axios'

export default function StudentList() {
  const columns = [
    {
        name: "Form No",
        options: {
         filter: false,
         sort: false,
         display: false,
        }
    },
    {
        name: "Reg No",
        options: {
         filter: false,
         sort: false,
         display: true
        }
    },
    {
        name: "Name",
        options: {
         filter: false,
         sort: true,
         display: true
        }
    },
    {
        name: "Parent",
        options: {
         filter: false,
         sort: false,
         display: false
        }
    },
    {
        name: "Address",
        options: {
         filter: false,
         sort: false,
         display: false
        }
    },
    {
        name: "Email",
        options: {
         filter: false,
         sort: false,
         display: false
        }
    },
    {
        name: "Mobile",
        options: {
         filter: false,
         sort: false,
         display: false
        }
    },
    {
        name: "Gender",
        options: {
         filter: true,
         sort: false,
         display: true,
        }
    },
    {
        name: "Category",
        options: {
         filter: true,
         sort: true,
         display: true
        }
    },
    {
        name: "Course",
        options: {
         filter: true,
         sort: true,
         display: true
        }
    },
    {
        name: "Sem",
        options: {
         filter: true,
         sort: true,
         display: true
        }
    },
    {
        name: "Roll No",
        options: {
         filter: false,
         sort: true,
         display: true
        }
    },
    {
        name: "Batch",
        options: {
         filter: true,
         sort: false,
         display: false,
         hint: "Batch Year"
        }
    },
    {
        name: "Father/Gardian Occupation",
        options: {
         filter: false,
         sort: false,
         display: false
        }
    },
    {
        name: "Fater/Guardian Income",
        options: {
         filter: false,
         sort: false,
         display: false
        }
    },
    {
        name: "Bank Name",
        options: {
         filter: false,
         sort: false,
         display: false
        }
    },
    {
        name: "Bank Branch",
        options: {
         filter: false,
         sort: false,
         display: false
        }
    },
    {
        name: "Account No",
        options: {
         filter: false,
         sort: false,
         display: false
        }
    },
    {
        name: "IFSC Code",
        options: {
         filter: false,
         sort: false,
         display: false
        }
    },
    {
        name: "Application Date",
        options: {
         filter: false,
         sort: false,
         display: false
        }
    },
]
  const options = {
    filter: true,
    filterType: "dropdown",
    responsive: "vertical",
    tableBodyHeight: "100%",
    tableBodyMaxHeight: 'standard',
    print:false,
    elevation:8,
    searchPlaceholder: 'Search student',
    draggableColumns:{enabled:true},
    rowsPerPageOptions: [10,20,50,100],
    downloadOptions: {filename: 'ascent.csv', separator: ',',filterOptions:{useDisplayedColumnsOnly: true,useDisplayedRowsOnly: true}}
    
  };
const [data, setdata]=useState([])
const [flag, setflag]=useState('')
  useEffect(() =>{
    axios.get(`api/scholarship/count/data/aa`, {})
    .then(res => {
        var persons=[];
        let array=res.data
        
        for(let i in array)
        {
            var temp=[];
            temp.push(array[i].id);
            temp.push(array[i].RegistrationNo);
            temp.push(array[i].Sfname + ' ' + (array[i].Smname===null?'':array[i].Smname) + ' ' + (array[i].Slname===null?'':array[i].Slname));
            temp.push(array[i].pFName + ' ' + (array[i].Pmname===null?'':array[i].Pmname) + ' ' + (array[i].Plname===null?'':array[i].Plname));
            temp.push(array[i].Address);
            temp.push(array[i].email);
            temp.push(array[i].Mobile);
            temp.push(array[i].gender);
            temp.push(array[i].category);
            temp.push(array[i].coursename);
            temp.push(array[i].semester);
            temp.push(array[i].ClassRollNo);
            temp.push(array[i].Batch);
            temp.push(array[i].gfoccupation);
            temp.push(array[i].gfincome);
            temp.push(array[i].bankname);
            temp.push(array[i].bankbranch);
            temp.push(array[i].accountno);
            temp.push(array[i].ifsccode);
            temp.push(array[i].dateofapplication);
            persons.push(temp);
        }
       
        setdata(persons)
      
    })
    .catch((error) => {
        console.log(error)
    })
},[flag])
  return (
    <React.Fragment>
     
      <MUIDataTable
        title={"List of Students Applied for Financial Assistance for the Year 2020"}
        data={data}
        columns={columns}
        options={options}
      />
    </React.Fragment>
  );
}
